const initAwesompleteFn = (selector) => {
  new Awesomplete(document.querySelector(selector), {
    list: follows.map((account) => `${account.username} (${account.acct})`),
    minChars: 0,
  });
};

export default (selector) => {
  if (typeof Awesomplete !== undefined) {
    initAwesompleteFn(selector);
  } else {
    setTimeout(() => {
      initAwesompleteFn(selector);
    }, 300);
  }
};
